@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,700,900&display=swap);
main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

html {
  overflow-y: scroll; }

body {
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#d9afd9), to(#97d9e1));
  background-image: -webkit-linear-gradient(bottom, #d9afd9 0%, #97d9e1 100%);
  background-image: linear-gradient(to top, #d9afd9 0%, #97d9e1 100%); }

ul {
  list-style: none; }

a {
  text-decoration: none; }
  a:visited {
    color: inherit; }

*,
*:after,
*:before {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

.show-phones {
  -webkit-animation: color-change 1s infinite;
          animation: color-change 1s infinite; }
  .show-phones:hover {
    -webkit-transform: scale(3.23);
            transform: scale(3.23); }

@-webkit-keyframes color-change {
  0% {
    color: deeppink; }
  50% {
    color: deepskyblue; }
  100% {
    color: deeppink; } }

@keyframes color-change {
  0% {
    color: deeppink; }
  50% {
    color: deepskyblue; }
  100% {
    color: deeppink; } }

.home-page {
  height: 100vh;
  color: white;
  text-align: center; }
  .home-page h1 {
    padding: 1em;
    margin-top: 1em;
    display: inline-block;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.308);
    background-color: rgba(0, 0, 0, 0.1); }

header {
  z-index: 3333;
  width: 100%;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2); }

.header-nav {
  color: #86cef0; }
  .header-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .header-nav__link {
    color: inherit;
    padding: 1em 2em;
    border-bottom: 2px solid transparent; }
    .header-nav__link:hover {
      cursor: pointer;
      -webkit-transition-duration: 0.2s;
              transition-duration: 0.2s;
      border-color: lightblue; }
  .header-nav__li {
    display: flex; }
    .header-nav__li:last-child .header-nav__link {
      display: flex;
      justify-content: center;
      align-self: center;
      align-items: center;
      height: 100%;
      padding: 0 2em; }
    .header-nav__li-active {
      color: lightblue;
      border-color: deeppink; }
      .header-nav__li-active:hover {
        border-color: deeppink; }
      .header-nav__li-active:visited {
        color: lightblue; }

.lds-ring {
  margin-top: 30vh;
  display: flex;
  width: 102px;
  height: 102px; }

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 102px;
  height: 102px;
  margin: 12px;
  border: 12px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: deeppink transparent transparent; }

.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s; }

.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s; }

.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s; }

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.notFound {
  text-align: center;
  padding: 3em;
  color: deeppink;
  text-shadow: 2px 2px 2px #000; }

.phones {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  max-width: calc((225px + 1em) * 4); }
  .phones__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center; }

.item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: calc(225px + 1em);
  padding: 0.5em;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  background-color: white;
  text-align: center;
  box-shadow: 0 0 0 1px rgba(228, 228, 228, 0.8); }
  .item:hover {
    box-shadow: 0 10px 13px -7px #000, 5px 5px 15px 5px rgba(138, 134, 134, 0);
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px); }
  .item__img {
    height: 225px; }
  .item__title {
    color: #86cef0; }
    .item__title::first-letter {
      text-transform: capitalize; }
    .item__title:hover {
      text-decoration: underline; }
  .item__description {
    color: black;
    font-size: 14px; }

.button-buy {
  font-size: 1.3em;
  padding: 0.35em 1em;
  margin-top: 1em;
  width: 100%;
  color: white;
  background-color: #86cef0;
  border: none;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  grid-row-end: [last-line]; }
  .button-buy:hover {
    cursor: pointer;
    background-color: #9fe99f; }

.details {
  display: flex;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.4); }

.phone {
  display: grid;
  max-width: 60vw;
  padding: 1em;
  grid-gap: 1em;
  grid-template-rows: auto;
  grid-template-columns: repeat(3, auto);
  grid-template-areas: "main-img title title " "main-img thumb thumb" "main-img description description" "main-img buy-button buy-button"; }
  .phone__main-img {
    grid-area: main-img;
    box-shadow: 0 0 0 2px lightblue; }
    .phone__main-img:hover {
      cursor: pointer; }
  .phone__about {
    grid-area: about; }
  .phone__title {
    grid-area: title; }
  .phone__description {
    grid-area: description; }
  .phone .button-buy {
    grid-area: buy-button; }

.thumb {
  grid-area: thumb; }
  .thumb__img {
    border: 2px solid lightgray;
    max-width: 100px;
    height: auto;
    margin: 0.1em;
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s; }
    .thumb__img:hover {
      cursor: pointer;
      border-color: lightblue;
      -webkit-transform: scale(1.1);
              transform: scale(1.1); }
    .thumb__img-active {
      border-color: lightblue; }

.basket {
  margin: auto;
  width: 50px;
  height: auto;
  color: lightblue; }
  .basket__link {
    padding: 0; }

.basket-table {
  margin-top: 3rem;
  text-align: center;
  border-collapse: collapse; }
  .basket-table__link-to-item {
    color: inherit; }
    .basket-table__link-to-item:hover {
      text-decoration: underline; }
    .basket-table__link-to-item:visited {
      color: inherit; }
  .basket-table__count-cell {
    display: flex;
    width: 100px;
    height: 120px;
    justify-content: space-around;
    align-items: center; }
  .basket-table tr {
    border: 1px solid lightblue; }
  .basket-table td {
    padding: 0.2em;
    background-color: rgba(240, 248, 255, 0.87); }

.basket-table__img {
  max-width: 110px;
  height: auto; }

.basket__empty {
  padding: 1em 3em;
  margin-top: 3rem;
  display: inline-block;
  color: white;
  font-size: 3em;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.308);
  background-color: rgba(0, 0, 0, 0.1); }

.filter {
  box-shadow: 0 0 0 1px rgba(228, 228, 228, 0.8);
  width: 100%;
  display: flex; }
  .filter__search-field {
    height: 44px;
    padding: 0.3em;
    font-size: 1.3em;
    width: 90%;
    border: none; }
    .filter__search-field::before {
      content: "";
      height: 44px;
      width: 44px;
      background: url("/img/search-icon.svg") left center no-repeat; }
  .filter__direction {
    font-size: 1.3em;
    text-align: center;
    width: 10%;
    border: none; }

.myFancyButton {
  width: 25px;
  height: 25px;
  color: lightblue;
  border: 1px solid lightblue;
  border-radius: 3px;
  background-color: white;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s; }
  .myFancyButton:hover {
    cursor: pointer;
    color: white;
    background-color: lightblue; }
  .myFancyButton--warning {
    border-color: deeppink;
    color: deeppink; }
    .myFancyButton--warning:hover {
      color: white;
      background-color: deeppink; }

